import jqueryMCustomScrollbarConcat from '../utils/jquery.mCustomScrollbar.concat.min';
window.$ = window.jQuery = require('jquery');
jqueryMCustomScrollbarConcat();
const scroll = () => {
    $('.mycustom-scroll').mCustomScrollbar({
        axis: 'x', //  
        scrollInertia: '300', //  ,   

        mouseWheel: {
            deltaFactor: 30, // -      
        },
    });
};

export default scroll;
